<template>
    <div class="square">
        <van-list
        v-if="data"
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
        >
            <ul >
                <li v-for="(item,key) in data" :key="key" @click="handleToDetails(item.tid)">
                    <div class="left">
                        <p>{{item.address}}</p>
                        <p>{{ item.dateline | timeformat()}}</p>
                    </div>
                    <div class="right">
                        <div class="image-box" v-if="item.image_list && item.attachment == 2 ? item.image_list.length :false" @click="handlePreviewImage(item.image_list )">
                            <img :src="item.image_list[0]" >
                            <span class="nums" v-if="item.image_list ? item.image_list.length > 1 :false">{{item.image_list.length}}</span>
                        </div>
                        <div class="video-box" v-bind:class="{'video_1_box':item.video_heng_shu == 1,'video_2_box':item.video_heng_shu == 0}" v-if="item.attachment == 1">
                            <video :src="item.video" :poster="item.image_list[0]" x5-video-player-type="h5" x5-video-orientation="landscape|portrait" controls="controls"></video>
                        </div>
                        <div class="">
                            <p class="text" v-bind:class="{'bkbabel':item.image_list ? !item.image_list.length :true}">{{item.message}}</p>
                        </div>
                    </div>
                </li>
            </ul>        
        </van-list>
    </div>
</template>

<script>
import {formatTimestamp} from '@js/moment'
import { ImagePreview } from 'vant';
export default {
    props:['uid','type'],
    data(){
        return {
            data:'',
            
            loading:false,
            finished:false,
            
            pageInfo:{
                page_size:10,
                page_index:0
            }
        }
    },
    created(){
        this.getMyThread(this.uid);
    },
    methods:{
        onLoad(){
            this.getMyThread(this.uid);
        },
        // 跳转详情
        handleToDetails(tid){
            let url = `${this.$store.state.indexUrl+'square/details?tid='+tid+'&'+this.$store.state.appParams}`,
                json = {url:url,share:'0',keyboard:'1'};
            if(!this.$store.state.userData){
                window.location.href= "teldapp://login"
            }else {
                window.location.href= `teldapp://newWebview?params=`+JSON.stringify(json)
                //  this.$router.push({path:'/square/details',query:{tid:tid}})
            }

        },
        // 图片浏览
        handlePreviewImage(img){
           // ImagePreview(img)
        },
        getMyThread(uid){
            this.api.getMyThread({
                token:this.$store.state.userData,
                uid:uid,
                type:this.type,
                special:0,
                page_size:this.pageInfo.page_size,
                page_index:this.pageInfo.page_index
            }).then(res => {
                this.loading = false;
                if(res.err_code == 0){
                    this.pageInfo.page_index++;
                    if(!res.data.my_thread_data.length){
                        this.finished = true;
                    }
                    this.data = [...this.data,...res.data.my_thread_data] 
                }
            })
        },
    },
    filters:{
        timeformat(v){
            return formatTimestamp(v*1000,'MM-DD')
        }
    }
}
</script>

