    padding: 0 15px;
    ul{
        li{
            display: flex;
            margin-bottom: 12px;
            .left{
                width: 47px;
                margin-right: 10px;
                flex: none;
                p{
                    font-size: 13px;
                    color: #4E5578
                }
            }
            .right{
                display: flex;
                .image-box{
                    width: 75px;
                    height: 75px;
                    position: relative;
                    margin-right: 8px;
                    img{
                        width: 100%;
                        height: 100%; 
                        object-fit:cover;
                    }
                    .nums{
                        position: absolute;
                        display: inline-block;
                        background-color: #4E5578;
                        font-size: 10px;
                        border-radius:50%;
                        color: #ffffff;
                        width: 12px;
                        height: 12px;
                        text-align: center;
                        line-height: 12px;
                        bottom: 0px;
                        right: 0px;
                    }
                }
                .video-box{
                    width: 75px;
                    height: 75px;
                    position: relative;
                    margin-right: 8px; 
                    video{
                        width: 100%;
                        height: 100%;
                    }                   
                }
                .text{
                    color: #000000;
                    font-size: 14px;
                    line-height:23px;
                }
                .bkbabel{
                    background-color: #EEEEEE;
                    padding: 10px 15px 10px 11px;
                }
            }
        }
    }

}
</style>